import React from 'react';
import Component from 'react-pure-render/component';
import text from 'data/text.yml';
import links from 'data/links.yml';
import linkify from 'components/linkify';

export default class Cards extends Component {

  render() {
    return (
      <div>
        <div className={styles.root}>
          <div className={styles.content}>
            <div className={styles.desc}>{linkify(text.projects.title[__LANG__])}</div>
          </div>
        </div>
        <div className={styles.root}>
          <div className={styles.cards}>
            <div className={styles.hovereffect}>
              <a href={links.innopolis1[__LANG__]}>
                <div className={styles.innopolis1}/>
                <div className={styles.overlay}>
                  <h2>{linkify(text.cards.innopolis.name[__LANG__])}</h2>{linkify(text.cards.innopolis.desc[__LANG__])}
                </div>
              </a>
            </div>
            <div className={styles.hovereffect}>
              <a href={links.innopolis2[__LANG__]}>
                <div className={styles.innopolis2}/>
                <div className={styles.overlay}>
                  <h2>{linkify(text.cards.innopolis.name[__LANG__])}</h2>{linkify(text.cards.innopolis.desc[__LANG__])}
                </div>
              </a>
            </div>
            <div className={styles.hovereffect}>
              <a href={links.xsollaPage1[__LANG__]}>
                <div className={styles.xsolla}/>
                <div className={styles.overlay}>
                  <h2>{linkify(text.cards.xsolla.name[__LANG__])}</h2>{linkify(text.cards.xsolla.desc[__LANG__])}
                </div>
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }

}



